import axios from 'axios';
import store from '@/stores';
import router from '@/router';
import { Message, MessageBox } from 'element-ui';
import { getToken, setToken } from '@/plugins/auth';
import { showFullScreenLoading, tryHideFullScreenLoading } from "./axiosInitHelper";
axios.defaults.baseURL = process.env.axios.URL_PERFIX;
axios.defaults.timeout = 15000;
axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; // axios.defaults.headers.common['Accept']

var token = getToken();

if (token) {
  axios.defaults.headers.common['Authorization'] = "Bearer ".concat(token);
} // 


axios.interceptors.request.use(function (config) {
  // 
  // config.showLoading = true
  // 
  // if (config.showLoading) {
  //     showFullScreenLoading()
  // }
  return config;
}, function (error) {
  return Promise.reject(error);
}); // 

axios.interceptors.response.use(function (response) {
  // if (response.config.showLoading) {
  //     tryHideFullScreenLoading()
  // }
  //  token token  token token 
  var token = response.headers.authorization;

  if (token) {
    //  header  token refreshToken  token
    setToken(token);
  }

  if (response.status !== 200) {
    return Promise.reject('errCode' + response.status);
  } else {
    var res = response.data;

    switch (res.respCode) {
      //  respCode  400
      case 400:
        Message.error(res.errMsg);
        break;
      // token

      case 401:
        // Message.error('')
        store.dispatch('logout').then(function () {
          router.push({
            name: "Login"
          });
        });
        break;
    }

    if (res.respCode === 200) {
      return res.data;
    } else {
      return Promise.reject('errMsg' + res.errMsg);
    }
  }
}, function (error) {
  // tryHideFullScreenLoading();
  return Promise.reject(error);
});
export default axios;